      <div class="title">
        <h1 class="page-title span10 offset1">常见问题</h1>
      </div>
      <div class="container">
        <dl>
          <dt>斐泉是来自斐济的吗？</dt>
          <dd>
            <p>斐泉百分百来自原生态热带岛屿-斐济，位于南太平洋由300多个岛屿组成，远离最近的工业国家1,600英里。斐泉的取水地点是维提岛（斐济两个主要岛屿之一）的Yaqara Valley。</p>
          </dd>

          <dt>什么是“天然自流水蓄水层”？</dt>
          <dd>
            <p>天然自流水蓄水层是深入地下的天然加压幽闭地下蓄水层。经过层层的火山岩过滤后，流入一个岩壁围着的蓄水层。蓄水层中的水有岩壁保护着，不会接触任何污染元素。时间优化水质，将滴水变成甘泉。斐泉来自天然自流水蓄水层。</p>
          </dd>

          <dt>斐泉的特点有哪些？</dt>
          <dd>
            <p>斐泉如此与众不同是因其独有的的水源-斐济Viti Levu群岛上一个偏远的，受保护的地底深层自流水层，独特矿物质成分赋予了斐泉清新爽滑柔软的独特口感。在你打开瓶盖之前，没有人为接触，维护了它的天然水品质。</p>
          </dd>

          <dt>斐泉 PH值是多少？</dt>
          <dd>
            <p>斐泉的PH值为 7.5 ± 0.5。纯净水的pH值为7。饮用水和天然水的PH值会由于其含有矿物质和气体而有所不同。pH值低于7的被认为是酸性，高于7的被认为是碱性。</p>
          </dd>
        </dl>
      </div>
    </div>
