      <a href="javascript:;">视频2</a>
      <a href="javascript:;">视频3</a>
      <a href="javascript:;">视频4</a>
    </div>
  </div>
</template>

<script>

export default {
  name: "VideosComponent",
  components: {},
  data() {
    return {
      videoSrcList: [
        {
          name: "斐泉故事",
          srcMp4: require('@/assets/video/1.mp4'),
          srcWebm:require('@/assets/video/1.webm'),
        }
      ]
    };
  },
  created() {},
  methods: {
    getKey: function(index) {
      return index + new Date().getTime().toString();
    }
  }
};
</script>

