<template>
  <div class="nav-list-container">
    <a href="javascript:;" class="logo-wrapper">
      <img :src="logoImgSrc" />
    </a>
    <ul class="nav-list-wrapper">
      <li class="nav-list" v-for="(nav, index) in navListData" :key="getKey(index)">
        <router-link :to="nav.path" class="nav">{{nav.name}}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import logoImgSrc from "@/assets/logo.png";

export default {
  name: "NavList",
  components: {},
  data() {
    return {
      logoImgSrc: logoImgSrc,
      navListData: [
        {
          name: "斐济矿泉",
          path: "/pages/index"
        },
        {
          name: "关于我们",
          path: "/pages/about"
        },
        {
          name: "视频介绍",
          path: "/pages/videos"
        },
        {
          name: "常见问题",
          path: "/pages/faqs"
        },
        {
          name: "联系我们",
          path: "/pages/concat"
        }
      ]
    };
  },
  computed: {},
  methods: {
    getKey: function(index) {
      return index + new Date().getTime().toString();
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/theme.scss";

.nav-list-container {
  background: $theme-color;
  height: 100vh;
  .logo-wrapper {
    position: relative;
    display: inline-block;
    width: 130px;
    height: 113px;
    img {
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  .nav-list-wrapper {
    list-style: none;
    border-top: 1px solid #1e3741;
    li {
      width: 100%;
      height: 40px;
      text-align: center;
      line-height: 40px;
      border-bottom: 1px solid #1e3741;
      .nav {
        display: block;
        text-decoration: none;
        color: #738085;
        transition: all 0.5s;
        font-size:13.5px;
        &:hover {
          color: #fff;
        }
        &.active {
          background: #1688dc;
          color:#fff;
        }
      }
    }
  }
}
</style>
