<template>
  <div class="index-container">
    <div class="swiper-container">
      <swiper :options="swiperOption" ref="mySwiper">
        <!-- slides -->
        <swiper-slide class="swiper-slide">
          <div class="mask-top">
            <div class="text-wrapper">
              <h1>高于斐济</h1>
              <span>距离最近的大陆1600多英里</span>
            </div>
          </div>
          <img src="https://www.fijiwater.com/content/dam/fijiwater/water/1Clouds1.jpg" />
        </swiper-slide>
        <swiper-slide class="swiper-slide">
          <div class="mask-top">
            <div class="text-wrapper">
              <h1>高于斐济</h1>
              <span>距离最近的大陆1600多英里</span>
            </div>
          </div>
          <img src="https://www.fijiwater.com/content/dam/fijiwater/water/2Island1-1160x870.jpg" />
        </swiper-slide>
        <swiper-slide class="swiper-slide">
          <div class="mask-top">
            <div class="text-wrapper">
              <h1>高于斐济</h1>
              <span>距离最近的大陆1600多英里</span>
            </div>
          </div>
          <img src="https://www.fijiwater.com/content/dam/fijiwater/water/3Jungle-1160x870.jpg" />
        </swiper-slide>
        <!-- Optional controls -->
        <div class="swiper-pagination" slot="pagination"></div>
        <div class="swiper-button-next swiper-button-black" slot="button-next"></div>
      </swiper>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "IndexsComponent",
  components: {},
  data() {
    return {
      swiperOption: {
        navigation: {
          nextEl: ".swiper-button-next"
        },
        speed: 1500,
        direction: "vertical",
        slidesPerView: 1,
        spaceBetween: 0,
        mousewheel: true,
        on: {
          transitionStart: function() {
            $(".swiper-slide")
              .find(".text-wrapper")
              .css({ top: "20%" });
            $(".swiper-slide")
              .eq(this.activeIndex)
              .find(".text-wrapper")
              .css({ top: "50%" });
          }
        }
      }
    };
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  mounted() {
    // current swiper instance
    // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
    // this.swiper.slideTo(1, 400, false);
  }
};
</script>

<style lang="scss" scoped>
// @import "src/styles/styles.scss";
.index-container {
  display: flex;
  flex-direction: row;
}
.swiper-container {
  width: 100%;
  height: 100vh;
  margin-left: auto;
  margin-right: auto;
}
.swiper-slide {
  position: relative;
  img {
    min-width: 100%;
    height: 100%;
  }
  .mask-top {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(#1587dc, 0.2);
    text-align: center;
    color: rgba(#fff, 0.8);
    box-sizing: border-box;
    div {
      position: absolute;
      top: 20%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: all 1.7s;
      &:nth-child(1) {
        top: 50%;
      }
    }
    h1 {
      font-size: 32px;
      font-weight: 500;
      margin-bottom: 20px;
    }
    span {
      font-size: 16px;
      font-weight: 200;
    }
  }
}
.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  top: auto;
  left: 50%;
  bottom: 20px;
  width: 50px;
  height: 50px;
  background-size: 16px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  transform: translate(-50%, 0) rotate(90deg);
}
.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
  display: none;
}
</style>
