import Vue from 'vue';
import Router from 'vue-router';
import PagesComponent from '@/pages/pages';
import IndexComponent from '@/pages/index/index';
import AboutComponent from '@/pages/about/about';
import VideosComponent from '@/pages/videos/videos';
import FaqsComponent from '@/pages/faqs/faqs';
import ConcatComponent from '@/pages/concat/concat';
Vue.use(Router);

export default new Router({
  // mode: 'history',  //remove '#'
  linkActiveClass: 'active',
  routes: [{
    path: '/pages',
    component: PagesComponent,
    children: [{
      path: '',
      component: IndexComponent,
      redirect: ''
    },
    {
      path: 'index',
      component: IndexComponent,
      meta: {
        title: '斐济矿泉 | 无人为接触，从未被污染。'
      }
    }, {
      path: 'about',
      component: AboutComponent,
      meta: {
        title: '斐济矿泉 | 关于我们'
      }
    },
    {
      path: 'videos',
      component: VideosComponent,
      meta: {
        title: '斐济矿泉 | 视频介绍'
      }
    },{
      path: 'faqs',
      component: FaqsComponent,
      meta: {
        title: '斐济矿泉 | 常见问题'
      }
    }, {
      path: 'concat',
      component: ConcatComponent,
      meta: {
        title: '斐济矿泉 | 联系我们'
      }
    },
    ]
  }, {
    path: "*",
    redirect: '/pages'
  }]
});
